import React from "react";
import styled from "styled-components";
import Navbar from 'react-bootstrap/Navbar';
import Nav from 'react-bootstrap/Nav'

const Navigations = () => {
  return (
    <Navbar variant="dark" sticky="top" style={{backgroundColor:"#161616"}} expand="lg">
  <Navbar.Brand style={{color: "white", fontWeight: "bolder", fontSize: "1.8rem"}} href="#home">VSearchX</Navbar.Brand>
  <Navbar.Toggle aria-controls="basic-navbar-nav" />
  <Navbar.Collapse className="justify-content-end" id="basic-navbar-nav">
  <Nav>
    <Nav.Item style={{paddingRight: "1.1rem", fontSize: "1.1rem", fontWeight: 600}}>
      <Nav.Link href="#home">Home</Nav.Link>
    </Nav.Item>
    <Nav.Item style={{paddingRight: "1.1rem", fontSize: "1.1rem", fontWeight: 600}}>
      <Nav.Link href="#about">About</Nav.Link>
    </Nav.Item>
    <Nav.Item style={{paddingRight: "1.1rem", fontSize: "1.1rem", fontWeight: 600}}>
      <Nav.Link href="#contact">Contact</Nav.Link>
    </Nav.Item>
  </Nav>
  </Navbar.Collapse>
</Navbar>
  );
};

export default Navigations;
