import React from "react";
import styled from "styled-components";
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import CustomCard from './../../layout/Card';
import {Skew} from './../../layout/Skew';


const AboutUsWrapper = styled.div`
  background-color: #eee;
  padding-top: 10rem;
`;

export const Title = styled.div`
text-align: center;
font-size: 3rem;
color: #EE3A49;
font-weight: bolder;
`

const Content = styled.div`
color:#161616;
font-size: 1.2rem;
font-weight: 400;
text-align: justify;
padding: 2rem 0rem;

`


const AboutUs = () => {
  return(
    <div>
    <AboutUsWrapper>
      <Container>
      <Title>
        WHAT ARE WE?
      </Title>
      <Content>
        We are VSearchX; a research community - where the inquisitive like-minded smart minds come together, work and brain-storm to solve problems; do scientific research and collaborate together to their best potential. If you are looking forward to write research/review papers for higher studies, or to work on manuscripts or want to get your work peer-reviewed and whole lot of research things, we are here to provide mentorship, support and guidance for your bright career.
      </Content>
      </Container>
      
      <Container style={{paddingTop:"2rem"}}>
      
      <Row>
      <Col lg={4} xs={{marginLeft: "7rem"}} style={{ paddingBottom:"2rem"}} >
      <CustomCard img="/assets/AboutUs/community.jpg" text="Some quick example text to build on the card title and make up the bulk of
            the card's content."></CustomCard>
    </Col>

    <Col lg={4}  style={{ paddingBottom:"2rem"}}>
    <CustomCard img="/assets/AboutUs/courses.jpg" text="Some quick example text to build on the card title and make up the bulk of
            the card's content."></CustomCard>

    </Col>

    <Col lg={4}  style={{ paddingBottom:"2rem"}}>

    <CustomCard img="/assets/AboutUs/research.jpg" text="Some quick example text to build on the card title and make up the bulk of
            the card's content."></CustomCard>

    </Col>

    </Row>
    </Container>
        
    </AboutUsWrapper>
    <Skew topColor="#eee" bottomColor="#EE3A49"></Skew>
    </div>
  )
}

export default AboutUs;


